body {
  font-family: "Open Sans", sans-serif;
}

nav {
  width: 100%;
  margin: 0 auto;
  background: #fff;
  padding: 50px 0;
  box-shadow: 0px 5px 0px #dedede;
}
nav ul {
  list-style: none;
  text-align: center;
}
nav ul li {
  display: inline-block;
}
nav ul li :hover {
  color: #91640f;
}

nav ul li {
  display: block;
  padding: 15px;
  text-decoration: none;
  color: rgb(138, 113, 113);
  font-weight: 600;
  text-transform: uppercase;
  margin: 0 10px;
}
nav ul li .nav-link {
  transition: all 2s;
}

nav ul li .nav-link:after {
  text-align: left;
  content: ".";
  margin: 0;
  opacity: 0;
}
nav ul li .nav-link:hover {
  background-color: #5aa1a3;
  z-index: 1;
}
nav ul li .nav-link:hover:after {
  z-index: -10;
  animation: fill 1s forwards;
  -webkit-animation: fill 1s forwards;
  -moz-animation: fill 1s forwards;
  opacity: 1;
}
@-webkit-keyframes nav {
  0% {
    width: 0%;
    height: 1px;
  }
  50% {
    width: 100%;
    height: 1px;
  }
  100% {
    width: 100%;
    height: 100%;
    background: #333;
  }
}
.heading {
  text-align: center;
